.zolo-post-category-wrap {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 10px;

    .zolo-category-name {
        font-weight: 600;
        color: #fff;
        transition: all 0.3s ease;
    }

    .zolo-category-count {
        font-size: 13px;
        color: #fff;
        background: rgba(255, 255, 255, 0.3);
        border-radius: 6px;
        transition: all 0.3s ease;
        padding: 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 1em;
        height: 1em;
        box-sizing: content-box;
    }

    .zolo-category-item {
        position: relative;
        background: #2b2d42;
        padding: 20px;
        border-radius: 8px;
        text-decoration: none !important;
        overflow: hidden;
    }

    .zolo-content {
        display: flex;
        align-items: center;
        gap: 15px;
        justify-content: space-between;
    }

    .zolo-category-text {
        color: #e0e0e0;
        font-size: 14px;
        transition: all 0.3s ease;
        margin: 0;
        margin-bottom: 0 !important;
    }

    .zolo-category-img {
        height: 100%;
        width: 100%;
    }

    .zolo-category-img img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        vertical-align: middle;
    }

    .zolo-category-link {
        background-color: #ddd;
        padding: 10px;
        display: inline-flex;
        align-items: center;
        justify-content: space-between;
        font-size: 14px;
        text-transform: capitalize;
        font-weight: 600;
        gap: 5px;
        color: #29272e;
        border-radius: 6px;
        .zolo__display-icon svg {
            width: 1em;
            height: 1em;
            display: block;
            fill: currentColor;
            box-sizing: content-box;
        }
    }
    .zolo-category-link:hover {
        background-color: #fff;
    }

    &.zolo-category-style-1 {
        .zolo-category-item {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
        }
        .zolo-category-bottom-content {
            .zolo-category-link {
                margin-top: 15px;
            }
        }
    }
}
